                                   /*HOME*/

body {
	background: linear-gradient(-45deg, #7c2ae8, #fcc0c5, #23a6d5, #26f7b2);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
	overflow: hidden;
}

#root{
	overflow: hidden;
}

html, body {
  overflow-x: hidden;
  overflow-y: hidden;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.glassmorphism{
	background: linear-gradient( rgba(255, 255, 255, 0.125) 0%, rgba(255, 255, 255, 0.125) 100%);
	backdrop-filter: blur(80px);
	border-radius: 16px;
	width: 40vw;
	height: 50vh;
	position: absolute;
	top: 30vh;
	z-index: -1;
}

.flex{
	display: flex;
}

#deco-text1{
	font-size: 10vh;
	font-weight: 800;
}

#deco-text2{
	color: white;
	font-size: 10vh;
	font-weight: 500;
}

.deco{
	width: 120px;
	position: relative;
	z-index: 100;
}

.centered{
